import React, { useEffect, useState } from 'react'
import InfoBlock from '../InfoBlock/InfoBlock'

type TextData = {
    title?: string;
    desc?: string;
    items?: Array<{ id: string; title: string; desc: string }>;
};

function CookiesPage() {
    const [textData, setTextData] = useState<TextData>({
        title: 'Cookies Policy',
            desc: "<p><span style='text-align: start;color: rgb(0, 0, 0);'>This website uses third-party and own cookies to ensure that you have a better browsing experience, can share content on social networks, and so that we can obtain user statistics. You can avoid downloading cookies by configuring your browser settings, preventing cookies from being stored on your device. As the owner of this website, I inform you that we do not use any personal information from cookies, we only perform general visit statistics that do not involve any personal information. It is very important that you read this cookie policy and understand that if you continue browsing, we will consider that you accept its use. According to the terms included in article 22.2 of Law 34/2002 on Information Society Services and Electronic Commerce, if you continue browsing, you will be giving your consent for the use of the aforementioned mechanisms.<br>Responsible Entity<br>The entity responsible for collecting, processing, and using your personal data, in the sense established by the Personal Data Protection Law, is the Nordic Connect page, owned by  Calle Cardenal Beluga 1, 28001 Madrid.<br>What are cookies?<br>Cookies are a set of data that a server deposits in the user's browser to collect standard Internet registration information and visitor behavior information on a website. In other words, they are small text files that are stored on the computer's hard drive and serve to identify the user when they reconnect to the website. Their purpose is to record the user's visit and save certain information. Their use is common and frequent on the web as it allows pages to function more efficiently and achieve greater personalization and analysis of user behavior.<br>What types of cookies exist?<br>The cookies used on our website are session and third-party cookies, and they allow us to store and access information related to the language, the type of browser used, and other general characteristics predefined by the user, as well as track and analyze the activity carried out, in order to introduce improvements and provide our services in a more efficient and personalized way.<br>Cookies, depending on their permanence, can be divided into session cookies or permanent cookies. Those that expire when the user closes the browser. Those that expire depending on when the purpose for which they serve is fulfilled (for example, so that the user remains identified in the services of) or when they are manually deleted.<br>NameTypeExpirationPurposeClass__utmaThird-party (Google Analytics)2 yearsUsed to distinguish users and sessions.Not Exempt__utmbThird-party (Google Analytics)30 minutesUsed to determine new sessions or visitsNot Exempt__utmcThird-party (Google Analytics)At the end of the sessionConfigured for use with UrchinNot Exempt__utmzThird-party (Google Analytics)6 monthsStores the origin or campaign that explains how the user reached the websiteNot Exempt<br>Additionally, depending on their purpose, cookies can be classified as follows:<br>Performance cookies<br>This type of cookie remembers your preferences for the tools found in the services, so you don't have to reconfigure the service each time you visit. For example, in this type are included: Volume settings of video or sound players. Video transmission speeds that are compatible with your browser. Items saved in the shopping cart in e-commerce services such as stores.<br>Geolocation cookies<br>These cookies are used to find out in which country you are when a service is requested. This cookie is completely anonymous and is only used to help guide content to your location.<br>Registration cookies<br>Registration cookies are generated once the user has registered or subsequently opened their session, and are used to identify them in the services with the following objectives:<br>Keep the user identified so that, if they close a service, the browser, or the computer and at another time or another day they re-enter said service, they will remain identified, facilitating their navigation without having to re-identify themselves. This functionality can be suppressed if the user presses the [log out] functionality, so this cookie is eliminated, and the next time they enter the service the user will have to log in to be identified.<br>Check if the user is authorized to access certain services, for example, to participate in a contest.<br>Additionally, some services may use connectors with social networks such as Facebook or Twitter. When the user registers in a service with credentials from a social network, they authorize the social network to save a persistent Cookie that remembers their identity and guarantees them access to the services until it expires. The user can delete this Cookie and revoke access to the services through social networks by updating their preferences in the specific social network.<br>Analytical cookies<br>Each time a user visits a service, a tool from an external provider generates an analytical cookie on the user's computer. This cookie, which is only generated in the visit, will serve in future visits to the services of to anonymously identify the visitor. The main objectives pursued are:<br>Allow anonymous identification of browsing users through the cookie (identifies browsers and devices, not people) and therefore approximate counting of the number of visitors and their trend over time.Identify anonymously the most visited content and therefore the most attractive to users Know if the user accessing is new or repeats a visit.<br>Important: Unless the user decides to register in a service, the cookie will never be associated with any personal data that can identify them. These cookies will only be used for statistical purposes to help optimize the user experience on the site.<br>Advertising cookies<br>This type of cookie allows to expand the information of the ads shown to each anonymous user in the services of . Among other things, the duration or frequency of display of advertising positions, the interaction with them, or browsing patterns and/or user behaviors are stored as they help to form a profile of advertising interest. This way, they allow offering advertising according to the user's interests.<br>Third-party advertising cookies<br>In addition to the advertising managed by the websites in their services, the websites offer their advertisers the option to serve ads through third parties ('Ad-Servers'). In this way, these third parties can store cookies sent from the services of from the browsers of the users, as well as access the data stored in them.<br>The companies that generate these cookies have their own privacy policies. Currently, the websites use the Doubleclick platform (Google) to manage these services. For more information, visit<br>http://www.google.es/policies/privacy/ads/#toc-doubleclick and http://www.google.es/policies/privacy/ads/.<br>How can I disable cookies in my browser?<br>Different browsers can be configured to notify the user of the reception of cookies and, if desired, prevent their installation on the equipment. Likewise, the user can review in their browser what cookies they have installed and what is the expiration term of them, being able to eliminate them.<br>For more information, consult the instructions and manuals of your browser:<br>For more information on managing cookies in Google Chrome: https://support.google.com/chrome/answer/95647?hl=en<br>For more information on managing cookies in Internet Explorer: http://windows.microsoft.com/en-us/windows-vista/cookies-frequently-asked-questions<br>For more information on managing cookies in Mozilla Firefox: http://support.mozilla.org/en-US/kb/enable-and-disable-cookies-website-preferences<br>For more information on managing cookies in Safari: http://www.apple.com/privacy/use-of-cookies/<br>For more information on managing cookies in Opera: http://help.opera.com/Windows/11.50/en-EN/cookies.html<br>If you want to stop being followed by Google Analytics visit: http://tools.google.com/dlpage/gaoptout<br>To learn more about cookies<br>You can get more information about online advertising based on behavior and online privacy at the following link: http://www.youronlinechoices.com/en/<br>Google Analytics data protection: http://www.google.com/analytics/learn/privacy.html<br>How Google Analytics uses cookies: https://developers.google.com/analytics/devguides/collection/analyticsjs/cookie-usage?hl=en#analyticsjs<br>Updates and changes to the privacy/cookie policy<br>The websites can modify this Cookies Policy based on legislative, regulatory requirements, or with the purpose of adapting said policy to the instructions issued by the Spanish Data Protection Agency, for this reason, users are advised to visit it periodically.<br>When significant changes occur in this Cookie Policy, they will be communicated to users either through the web or via email to registered users.</span></p>"
    })

    return (
        <>
            <InfoBlock type={'terms'} textData={textData} />
        </>
    )
}

export default CookiesPage
