import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../../store"; //     store

//    
interface UsersState {
    userToken: string | null; // userToken   null
}

//    userToken  localStorage
const getUserTokenFromLocalStorage = (): string | null => {
    const token = localStorage.getItem('userToken');
    return token ? token : null;
};

//  
const initialState: UsersState = {
    userToken: getUserTokenFromLocalStorage(),
};

export const usersSlice = createSlice({
    name: 'users',
    initialState,
    reducers: {
        login: (state, action: PayloadAction<string>) => {
            state.userToken = action.payload;
            
            localStorage.setItem('userToken', action.payload); //    localStorage
        },
        logout: (state) => {
            state.userToken = null;
            localStorage.removeItem('userToken'); //    localStorage  logout
        }
    }
});
//   
export const usersSelector = (state: RootState): UsersState => state.users;

//    
export const { login, logout } = usersSlice.actions;
export const usersReducers = usersSlice.reducer;